import React from 'react';
import './App.css';
import './Fontawesome/css/fontawesome.css';
import './Fontawesome/css/solid.css';
import GlobalFonts from './assets/font/fonts';
import Homepage from './components/homepage';
import Rules from './components/rules';
import Mainsound from './components/mainSound';
import Setting from './pages/setting';
import GameResult from './pages/gameResult';
import Category from './pages/category';
import Popup from './components/popup';
import AboutUs from './components/about-us';
import {
    BrowserRouter as Router,
    Switch,
    Route
  } from "react-router-dom";
import {connect} from 'react-redux';
import {getSetting} from './redux/selector';
import Round from './pages/round';

function App(props) {

  return (
    <div className='App'>
      <div className={!props.Setting.ErrorJustHappened ? 'AppError' : 'AppError Active'}></div>
      <GlobalFonts/>
      <Router>
        <div style={props.Setting.blur ? {filter: 'blur(10px)',transition: 'all .2s'} : null}>
            <Mainsound/>
            <Switch>
                <Route path='/' exact={true} component={Homepage}/>
                <Route path='/rules' component={Rules}/>
                <Route path='/setting' component={Setting}/>
                <Route path='/gameresult' component={GameResult}/>
                <Route path='/category' component={Category}/>
                <Route path='/round' component={Round}/>
                <Route path='/about-us' component={AboutUs}/>
                <Route path='*' component={Homepage}/>
            </Switch>   
        </div>
        {props.Setting.blur ? <Popup/> : null}
      </Router>
    </div>
  );
}

export default connect(store => ({Setting: getSetting(store)}),null)(App);;
