import * as Actions from './actionTypes';

export const blur = () => {
    return {
        type: Actions.BLUR,
        payload: {}
    }
}

export const addGroup = name => {
    return {
        type: Actions.ADD_GROUP,
        payload: {
        name
        }
    }
}

export const removeGroup = () => {
    return {
        type: Actions.REMOVE_GROUP,
        payload: {}
    }
}

export const editGroup = (id,newValue) => {
    return {
        type: Actions.EDIT_GROUP,
        payload: {
            id,
            newValue
        }
    }
}

export const addLap = () => {
    return {
        type: Actions.ADD_LAP,
        payload: {}
    }
}

export const removeLap = () => {
    return {
        type: Actions.REMOVE_LAP,
        payload: {}
    }
}

export const increaseThinkingTime = () => {
    return {
        type: Actions.INCREASE_THINKING_TIME,
        payload: {}
    }
}

export const decreaseThinkingTime = () => {
    return {
        type: Actions.DECREASE_THINKING_TIME,
        payload: {}
    }
}

export const selectCategory = (cat_id) => {
    return {
        type: Actions.SELECT_CATEGORY,
        payload: {
            cat_id
        }
    }
}

export const selectPoint = (point) => {
    return {
        type: Actions.SELECT_POINT,
        payload: {
            point
        }
    }
}

export const setWordChanged = (changed) => {
    return {
        type: Actions.SET_WORD_CHANGED,
        payload: {
            changed
        }
    }
}

export const increaseErrors = () => {
    return {
        type: Actions.INCREASE_ERRORS,
        payload: {}
    }
}

export const setErrorJustHappened = (state) => {
    return {
        type: Actions.SET_ERROR_JUST_HAPPENED,
        payload: {
            state
        }
    }
}

export const setErrorsToZero = () => {
    return {
        type: Actions.SET_ERRORS_TO_ZERO,
        payload: {}
    }
}

export const updatePoints = (score,remainingTime,index) => {
    return {
        type: Actions.UPDATE_POINTS,
        payload: {
            score,
            remainingTime,
            index
        }
    }
}

export const changeTurn = (turn) => {
    return {
        type: Actions.CHANGE_TURN,
        payload: {
            turn
        }
    }
}

export const changeLap = () => {
    return {
        type: Actions.CHANGE_LAP,
        payload: {}
    }
}

export const finishGame = () => {
    return {
        type: Actions.FINISH_GAME,
        payload: {}
    }
}