import React, { Component } from 'react';
import styled from 'styled-components';
import star from '../assets/img/star.png';
import actor from '../assets/img/actor.png';
import Backpage from '../components/backpage';
import {Link} from 'react-router-dom';
import {addGroup,removeGroup,editGroup,addLap,removeLap,increaseThinkingTime,decreaseThinkingTime} from '../redux/actions';
import {connect} from 'react-redux';
import {getSetting} from '../redux/selector';
import * as Actions from '../redux/actionTypes';
import createdb from '../database/createdb';

class Setting extends Component {

    componentDidMount() {
        createdb();
    }

    Container = styled.div`
        width: 95vw;
        padding: 5rem 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    `;

    Banner = styled.div`
        width: 100%;
        height: 3rem;
        background-color: #fff;
        border-radius: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        font-size: 1.3rem;
        margin: 1.5rem 0 .5rem;
    `;

    Modes = styled.div`
        width: 100%;
        height: 12rem;
        background-color: #fff;
        border-radius: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        font-size: 1.3rem;
    `;

    Counter = styled.div`
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        & button {
            width: 2.5rem;
            height: 2.5rem;
            background-color: #fff;
            border: none;
            border-radius: 2rem;

            &:focus {
                outline: none;
            }
        }

        & p {
            width: 3.5rem;
            height: 3.5rem;
            background-color: #fff;
            border-radius: 2rem;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    `;

    Input = styled.input`
        width: 90%;
        height: 2.5rem;
        padding: 0 1rem;
        background-color: #fff;
        border-radius: 2rem;
        border: none;
        margin: .5rem 0;
        font-family: inherit;

        &:focus {
            outline: none;
        }
    `;

    Startbtn = styled.button`
        padding: 0 1rem;
        height: 3rem;
        background-color: #fff;
        margin: 1rem 0;
        border: none;
        border-radius: 2rem;
        font-family: inherit;
        font-size: 1.2rem;
        
        & a {
            text-decoration: none;
            color: #333;
        }
    `;

    state = {
        value1: 'گروه 1',
        value2: 'گروه 2',
        value3: 'گروه 3',
        value4: 'گروه 4'
    }

    handleSetting = (action) => {
        switch (action) {
            case Actions.ADD_GROUP:
                if(this.props.Setting.GroupName.length < 4) {
                    this.props.addGroup('گروه');
                }
                break;

            case Actions.REMOVE_GROUP:
                if(this.props.Setting.GroupName.length > 2) {
                    this.props.removeGroup();
                }
                break;

            case Actions.ADD_LAP:
                if(this.props.Setting.Laps < 20) {
                    this.props.addLap();
                }
                break;

            case Actions.REMOVE_LAP:
                if(this.props.Setting.Laps > 3) {
                    this.props.removeLap();
                }
                break;

            case Actions.INCREASE_THINKING_TIME:
                if(this.props.Setting.ThinkingTime < 60) {
                    this.props.increaseThinkingTime();
                }
                break;

            case Actions.DECREASE_THINKING_TIME:
                if(this.props.Setting.ThinkingTime > 5) {
                    this.props.decreaseThinkingTime();
                }
                break;

            default:
                break;
        }
    }

    handleGroupNameChange = (e,index) => {
        switch (index) {
            case 0:                    
                    this.setState({
                        value1: e.target.value
                    })
                    break;
            case 1:
                    this.setState({
                        value2: e.target.value
                    })
                    break;
            case 2:
                    this.setState({
                        value3: e.target.value
                    })
                    break;
            case 3:
                    this.setState({
                        value4: e.target.value
                    })
                    break;
            default:
                break;
        }
    }

    handleGroupNameSave = () => {
        this.props.editGroup(0,this.state.value1);
        this.props.editGroup(1,this.state.value2);
        this.props.editGroup(2,this.state.value3);
        this.props.editGroup(3,this.state.value4);
    }

    render() {
        return (
            <>
                <Backpage to='/'/>
                <this.Container>
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>حالت بازی</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Modes>
                        <img src={actor} alt='actor'/>
                        <p>معمولی</p>
                    </this.Modes>
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>تعداد گروه ها</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Counter>
                        <button onClick={() => this.handleSetting(Actions.ADD_GROUP)}>
                            <i className='fa fa-plus'></i>
                        </button>
                        <p>{this.props.Setting.GroupName.length}</p>
                        <button onClick={() => this.handleSetting(Actions.REMOVE_GROUP)}>
                            <i className='fa fa-minus'></i>
                        </button>
                    </this.Counter>
                    {this.props.Setting.GroupName.map((item,index) => {
                        return (
                            <this.Input key={index} defaultValue={item.name} onChange={(e) => this.handleGroupNameChange(e,index)}/>
                        )
                    })}
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>تعداد دورها</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Counter>
                        <button onClick={() => this.handleSetting(Actions.ADD_LAP)}>
                            <i className='fa fa-plus'></i>
                        </button>
                        <p>{this.props.Setting.Laps}</p>
                        <button onClick={() => this.handleSetting(Actions.REMOVE_LAP)}>
                            <i className='fa fa-minus'></i>
                        </button>
                    </this.Counter>
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>زمان فکر کردن</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Counter>
                        <button onClick={() => this.handleSetting(Actions.INCREASE_THINKING_TIME)}>
                            <i className='fa fa-plus'></i>
                        </button>
                        <p>{this.props.Setting.ThinkingTime}</p>
                        <button onClick={() => this.handleSetting(Actions.DECREASE_THINKING_TIME)}>
                            <i className='fa fa-minus'></i>
                        </button>
                    </this.Counter>
                    <this.Startbtn onClick={() => this.handleGroupNameSave()}>
                        <Link to='/gameresult'>مرحله بعد</Link>
                    </this.Startbtn>
                </this.Container>
            </>
        );
    }
}

export default connect(store => ({Setting: getSetting(store)}),{addGroup,removeGroup,editGroup,addLap,removeLap,increaseThinkingTime,decreaseThinkingTime})(Setting);