import React, { Component } from 'react';
import Backpage from './backpage';
import styled from 'styled-components';

class Rules extends Component {

    rules = [
        'هر تیم میتواند یک نفر را به عنوان داور بی طرف انتخاب کند',
        'لب زدن و اشاره به اشیا در حین بازی خطا محسوب میشود',
        'اگر اجراکننده تیم در حین اجرا مرتکب 3 خطا شود تیم وی در ان دور بازنده خواهد شد',
        'به اضای هر 30 ثانیه اضافه 1 امتیاز به اجرا کننده تعلق خواهد گرفت'
    ];

    Container = styled.div`
        margin: 7rem 3% 1rem 3%;
        width: 94%;
    `;

    Item = styled.div`
        border-radius: 2rem;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: .5rem;
        margin: .5rem 0;

        & span {
            width: 95%;
            display: flex;
            align-items: flex-start;
            margin: .5rem 0;

            & i {
                color: #fcb400;
                margin: .3rem;
            }

            & p {
                margin-right: .1rem;
                text-align: right;
            }
        }
    `;

    render() {
        return (
            <>
                <Backpage to='/'/>
                <this.Container>
                    {this.rules.map((rule,index) => {
                        return (
                            <this.Item key={index}>
                                <span>
                                    <i className="fas fa-dot-circle"/>
                                    <p>{rule}</p>
                                </span>
                            </this.Item>
                        )
                    })}
                </this.Container>
            </>
        );
    }
}

export default Rules;
