import React, { Component } from 'react';
import {connect} from 'react-redux';
import {getSetting} from '../redux/selector';
import styled from 'styled-components';
import star from '../assets/img/star.png';
import Backpage from '../components/backpage';
import {Link} from 'react-router-dom';

class GameResult extends Component {

    Container = styled.div`
        width: 95vw;
        padding: 6rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    `;

    Table = styled.div`
        width: 100%;      
        display: flex;
        flex-direction: column;
        align-items: center;

        & div {
            width: 100%;
            height: 3rem;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            background-color: #fff;
            border-radius: 2rem;
        }

        & table {
            width: 100%;
            text-align: right;
            margin: .5rem 0;
            background-color: #fff;
            border-radius: 2rem;
            padding: 1rem;
        }

        & table thead tr td {
            padding: .5rem 0;
            
        }

        & table tbody tr td {
            color: #777;
            padding: .3rem .3rem;
            border-top: .1rem solid #ddd;
        }
    `;

    Startbtn = styled.button`
    padding: 0 1rem;
    height: 3rem;
    background-color: #fff;
    margin: 1rem 0;
    border: none;
    border-radius: 2rem;
    font-family: inherit;
    font-size: 1.2rem;

    & a {
        text-decoration: none;
        color: #111;
    }
    `;

    state = {
        winner: null
    }

    componentDidMount() {
        let groupScores = [];
        this.props.Setting.GroupName.map(item => {
            groupScores.push(item.score);
        })        
        const maxScore = Math.max(...groupScores);        
        this.props.Setting.GroupName.map(item => {
            if(item.score == maxScore) {
                this.setState({
                    winner: item.name
                })
            }
        })
    }

    render() {
        return (
            <>
                <Backpage to='/setting'/>
                <this.Container>
                    <this.Table>
                        <div>
                            <img src={star} alt='Star'/>
                            {!this.props.Setting.GameFinished ? <p>دور {this.props.Setting.CurrentLap} از {this.props.Setting.Laps}</p>
                            : <p>{this.state.winner} برنده شد</p>}
                            <img src={star} alt='Star'/>
                        </div>
                        <table>
                            <thead>
                                <tr>
                                    <td>گروه</td>
                                    <td>امتیاز</td>
                                </tr>
                            </thead>
                            <tbody>
                                {this.props.Setting.GroupName.map((item,index) => {
                                    return (
                                        <tr key={index}>
                                            <td>{item.name}</td>
                                            <td>{item.score}</td>
                                        </tr>
                                    )
                                })}
                            </tbody>
                        </table>
                        <div>
                            <img src={star} alt='Star'/>
                            <p>نوبت {this.props.Setting.GroupName[this.props.Setting.Turn-1].name}</p>
                            <img src={star} alt='Star'/>
                        </div>
                    </this.Table>
                    <this.Startbtn>
                        {!this.props.Setting.GameFinished ? 
                        <Link to='/category'>ادامه</Link> : <Link to='/'>صفحه اصلی</Link>}
                    </this.Startbtn>
                </this.Container>
            </>
        );
    }
}

export default connect(store => ({Setting: getSetting(store)}),null)(GameResult);
