import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

class homepage extends Component {

    Container = styled.div`
        width: 95vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;

        & .start {
        padding: 1rem 1.4rem;
        border-radius: 50%;
        color: rgb(250, 250, 250);
        font-size: 5rem;
        margin-bottom: 12rem;
        &:focus {
            outline: none;
        }
        }

        & .app-bottom {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-around;
            margin-bottom: 1rem;

            & a {
                width: 5rem;
                text-decoration: none;
                color: #fff;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-around;
                line-height: 2rem;
                &:focus {
                    outline: none;
                }
            }
        }

        & .app-bottom i {
            font-size: 2rem;
            color: rgb(250, 250, 250);
        }
    `;

    render() {
        return (
            <this.Container>
                <Link to='/setting' className='start'>
                    <i className="fas fa-play"/>
                </Link>
                <div className='app-bottom'>
                    <Link to='/rules'>
                        <i className="fas fa-gavel"/>
                        قوانین
                    </Link>
                    <Link to='/about-us'>
                        <i className="fas fa-user"/>
                        درباره ما
                    </Link>
                    <a href='https://zil.ink/me2002'>
                        <i className="fas fa-phone"/>
                        تماس بامن
                    </a>
                </div>
            </this.Container>
        );
    }
}

export default homepage;
