import React, { Component } from "react";
import styled from "styled-components";
import getdb from '../database/getdb';
import {connect} from 'react-redux';
import {getSetting} from '../redux/selector';
import {setWordChanged,increaseErrors,updatePoints,changeTurn,changeLap,finishGame,setErrorsToZero,setErrorJustHappened} from '../redux/actions';
import {Link} from 'react-router-dom';
import '../css/round.css';

class Round extends Component {
  Container = styled.div`
    padding: 7rem 0 1rem;
    width: 95vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  `;

  Buttons = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  `;

  TopButtons = styled.span`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 1rem 0;
  `;

  ControlButtons = styled.div`
    width: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    margin-top: 2rem;

    & p {
      font-size: .8rem;
      margin-top: .5rem
    }
  `;

  Word = styled.p`
    width: 100%;
    padding: 1rem 0;
    background-color: #fff;
    border-radius: 2rem;
    text-align: center;
    font-size: 1.3rem;
    color: #333;
  `;

  Button = styled.button`
    width: 4rem;
    height: 4rem;
    background-Color: #fff;
    border-Radius: 2rem;
    display: flex;
    align-Items: center;
    justify-Content: center;
    border: none;
    outline: none;
    color: #333;
    position: relative;

    & div {
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      left: 0%;
      top: 0%;
      background-color: #E53935;
      border-radius: 50%;
      color: #fff;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  `;

  FinishingButtons = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  `;

  thinkingTimeBox = styled.div`
    width: 5rem;
    height: 5rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    border-radius: 50%;
    font-size: 2rem;
  `;

  state = {
    word: [],
    time: null,
    started: false,
    finished: false,
    thinkingTime: null,

  }

  setWord = () => {    
    let result = getdb(String(this.props.Setting.SelectedCategory),String(this.props.Setting.SelectedPoint));
    result.then(res => {      
      this.setState({
        word: res
      })
    });
  }

  componentDidMount() {    
    this.setWord();
    this.setState({
      time: this.props.Setting.SelectedPoint*30 + 30
    })
    this.props.setWordChanged(false);
    this.props.setErrorsToZero();
    this.setState({
      thinkingTime: this.props.Setting.ThinkingTime
    })
    let thinkingTime = setInterval(() => {
      if(this.state.thinkingTime > 0) {
        this.setState(prev => {
          return  {
            thinkingTime: prev.thinkingTime - 1
          }
        })
      }
      else {
        this.handlePlay();
        clearInterval(thinkingTime);
      }
    },1000)
  }

  handleChangeWord = () => {    
    this.setWord();
    this.props.setWordChanged(true);
  }

  gameFinished = () => {
    clearInterval(this.myInterval);
    this.setState({
      finished: true
    })
  }

  handleError = () => {
    const Errors = this.props.Setting.Errors;
    const ErrorLimit = 3;
    if(Errors < ErrorLimit) {
      setTimeout(() => {
        this.props.setErrorJustHappened(false);
      },1000)
      this.props.increaseErrors();

      if(Errors == ErrorLimit-1) {
        setTimeout(() => {
          this.props.history.push('/gameresult');
        },1000)
      }
    }
  }

  handlePlay = () => {
    this.setState({
      started: true
    })
    this.myInterval = setInterval(() => {
      if (this.state.time > 0) {
        this.setState(prev => {
          return {
            time: prev.time - 1
          }
        })
      }
      if (this.state.time === 0) {
        this.gameFinished();
      }
    }, 1000)
  }

  handleFinished = () => {
    this.gameFinished();
  }

  handleRight = () => {
    this.props.updatePoints(this.props.Setting.SelectedPoint,this.state.time,this.props.Setting.Turn-1);    
    if(this.props.Setting.Turn < this.props.Setting.GroupName.length) {
      this.props.changeTurn(this.props.Setting.Turn+1)
    }
    else {
      if(this.props.Setting.CurrentLap == this.props.Setting.Laps) {
        this.props.finishGame();
      }
      else {
        this.props.changeLap();
      }
      this.props.changeTurn(1);
    }
  }

  handleFalse = () => {
    if(this.props.Setting.Turn < this.props.Setting.GroupName.length) {
      this.props.changeTurn(this.props.Setting.Turn+1)
    }
    else {
      if(this.props.Setting.CurrentLap == this.props.Setting.Laps) {
        this.props.finishGame();
      }
      else {
        this.props.changeLap();
      }
      this.props.changeTurn(1);
    }
  }

  render() {
    return (
      <this.Container>
        <this.Word>{this.state.word}</this.Word>
        {!this.state.started ? <this.thinkingTimeBox>{this.state.thinkingTime}</this.thinkingTimeBox> : null}
        <this.Buttons>
          <this.TopButtons>
            {this.state.started ?
            <this.ControlButtons>
              <this.Button onClick={() => this.handleError()}>
                <i className="fa fa-exclamation" style={{fontSize: '1.5rem'}}/>
                {this.props.Setting.Errors > 0 ? <div>{this.props.Setting.Errors}</div> : null}
              </this.Button>
              <p>خطا</p>
            </this.ControlButtons> :
            <this.ControlButtons>
             <this.Button disabled={this.props.Setting.isWordChanged ? true : false} style={this.props.Setting.isWordChanged ? {backgroundColor:'#ccc',color:'#666'} : null} onClick={() => this.handleChangeWord()}>
              <i className="fa fa-redo-alt" />
             </this.Button>
             <p>تعویض کلمه</p>
            </this.ControlButtons>
            }
            <div className='timer' style={this.state.started && !this.state.finished ? {animationName: 'timer'} : null}>{this.state.time}</div>
            <this.ControlButtons>
              <this.Button onClick={() => {this.state.started ? this.handleFinished() : this.handlePlay()}}>
                {this.state.started ? 
                <i className="fa fa-stop"/> : 
                <i className="fa fa-play"/>}
              </this.Button>
              <p>
                {this.state.started ? 'پایان' : 'شروع'}
              </p>
            </this.ControlButtons>
          </this.TopButtons>
          {this.state.finished ?
          <this.FinishingButtons>
          <Link to='/gameresult' style={{textDecoration: 'none'}} onClick={() => this.handleRight()}>
            <this.Button>
              <i className="fa fa-check" style={{color: '#388E3C',fontSize: '1.5rem'}}/>
            </this.Button>
          </Link>
          <Link to='/gameresult' style={{textDecoration: 'none'}} onClick={() => this.handleFalse()}>
            <this.Button>
              <i className="fa fa-times" style={{color: '#E53935',fontSize: '1.5rem'}}/>
            </this.Button>
          </Link>
        </this.FinishingButtons> : null}
        </this.Buttons>
      </this.Container>
    );
  }
}

export default connect(store => ({Setting: getSetting(store)}),{setWordChanged,increaseErrors,updatePoints,changeTurn,changeLap,finishGame,setErrorsToZero,setErrorJustHappened})(Round);
