import * as Actions from '../actionTypes';

const initialState = {
    GroupName: [
        {
            name: 'گروه 1',
            score: 0
        },
        {
            name: 'گروه 2',
            score: 0
        }
    ],
    Laps: 3,
    CurrentLap: 1,
    ThinkingTime: 5,
    SelectedCategory: null,
    SelectedPoint: null,
    isWordChanged: false,
    Errors: 0,
    ErrorJustHappened: false,
    RemainingTime: 0,
    Turn: 1,
    GameFinished: false,
    blur: false
}

let groupNumber = 2;

export default function (state = initialState,action) {
    switch (action.type) {

        case Actions.BLUR:
            return {
                ...state,
                blur: !state.blur
            }

        case Actions.ADD_GROUP:
            {
            groupNumber++;
            const {name} = action.payload;
            return {
                ...state,
                GroupName: [
                    ...state.GroupName,
                    {
                        name: name + ' ' + groupNumber,
                        score: 0
                    }
                ]
            }
        }

        case Actions.REMOVE_GROUP:
            {
            groupNumber--;
            let newGroupName = [];
            const id = state.GroupName.length-1;

            for(let i = 0;i < state.GroupName.length; i++) {
                if(id !== i) {
                    newGroupName.push(state.GroupName[i]);
                }
            }

            return {
                ...state,
                GroupName: newGroupName
            }
        }

        case Actions.EDIT_GROUP:
            {
            const id = action.payload.id;
            const newValue = action.payload.newValue;

            let newGroupName = [];

            for(let i = 0;i < state.GroupName.length; i++) {
                if(id !== i) {
                    newGroupName.push(state.GroupName[i]);
                }
                else {
                    state.GroupName[i] = {
                        name: newValue,
                        score: 0
                    }
                    newGroupName.push(state.GroupName[i]);
                }
            }

            return {
                ...state,
                GroupName: newGroupName
            }
        }

        case Actions.ADD_LAP:
            return {
                ...state,
                Laps: state.Laps + 1
            }

        case Actions.REMOVE_LAP:
            return {
                ...state,
                Laps: state.Laps - 1
            }

        case Actions.INCREASE_THINKING_TIME:
            return {
                ...state,
                ThinkingTime: state.ThinkingTime + 5
            }

        case Actions.DECREASE_THINKING_TIME:
            return {
                ...state,
                ThinkingTime: state.ThinkingTime - 5
            }

        case Actions.SELECT_CATEGORY:
            const {cat_id} = action.payload;
            return {
                ...state,
                SelectedCategory: cat_id
            }

        case Actions.SELECT_POINT:
            const {point} = action.payload;
            return {
                ...state,
                SelectedPoint: point
            }

        case Actions.SET_WORD_CHANGED:

            return {
                ...state,
                isWordChanged: action.payload.changed
            }

        case Actions.INCREASE_ERRORS:
            return {
                ...state,
                Errors: state.Errors + 1,
                ErrorJustHappened: true
            }

        case Actions.SET_ERROR_JUST_HAPPENED:
        return {
            ...state,
            ErrorJustHappened: action.payload.state
        }

        case Actions.SET_ERRORS_TO_ZERO:
            return {
                ...state,
                Errors: 0
            }

        case Actions.UPDATE_POINTS:
            const {score,remainingTime,index} = action.payload;
            let newGroupName = [];

            for(let i = 0;i < state.GroupName.length; i++) {
                if(index !== i) {
                    newGroupName.push(state.GroupName[i]);
                }
                else {
                    if(state.isWordChanged == true) {
                        state.GroupName[i] = {
                            name: state.GroupName[i].name,
                            score: state.GroupName[i].score + Number(score) - (Math.floor(state.Errors / 2)) + Math.floor(Number(remainingTime) / 30) - 1
                        }
                    }
                    else {
                        state.GroupName[i] = {
                            name: state.GroupName[i].name,
                            score: state.GroupName[i].score + Number(score) - (Math.floor(state.Errors / 2)) + Math.floor(Number(remainingTime) / 30)
                        }
                    }
                    
                    newGroupName.push(state.GroupName[i]);
                }
            }

            return {
                ...state,
                GroupName: newGroupName
            }

        case Actions.CHANGE_TURN:
            const Turn = action.payload.turn
            return {
                ...state,
                Turn
            }

        case Actions.CHANGE_LAP:
        return {
            ...state,
            CurrentLap: state.CurrentLap + 1
        }

        case Actions.FINISH_GAME:
            return {
                ...state,
                GameFinished: !state.GameFinished
            }

        default:
            return state;
    }
}