import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {connect} from 'react-redux';
import {getSetting} from '../redux/selector';
import {blur,selectPoint} from '../redux/actions';

class Popup extends Component {

    // Container = {
    //     width: '100%',
    //     height: '150vh',
    //     position: 'fixed',
    //     zIndex: '5',
    //     bottom: '-30%',
    //     display: 'flex',
    //     flexDirection: 'column',
    //     justifyContent: 'flex-end',
    //     backgroundColor: 'rgba(100,100,100,.5)',
    // };

    Container = styled.div `
        width: 100%;
        height: 150vh;
        position: fixed;
        z-index: 5;
        bottom: 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        background-color: rgba(100,100,100,.5);
        transform: translateY(30%);
        transition: all .2s;
    `;

    Scores = styled.div`
        width: 100%;
        background-color: #333;
        box-shadow: 0 .5rem .5rem #999;
        border-top-left-radius: 2rem;
        border-top-right-radius: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;

        & div {
            width: 80%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #eee;
            font-size: 1.3rem;
            margin: .5rem 0;

            & i {
                font-size: 1rem
            }
        }

        & p {
            color: #eee;
            
        }

        & a {
            width: 100%;
            height: 3rem;
            color: #fff;
            text-decoration: none;
            display: flex;
            flex-direction:column;
            align-items: center;
            justify-content: space-between;

            & i {
                width: 95%;
                background: linear-gradient(45deg, rgba(51,51,51,1) 0%, rgba(238,238,238,1) 20%, rgba(238,238,238,1) 50%, rgba(238,238,238,1) 80%, rgba(51,51,51,1) 100%);
                height: 1px;
            }
        }

        & a:last-child i {
                height: 0;
            }

        & a span {
            margin-top: 1rem;
        }
    `;

    Click = styled.div`
        width: 100%;
        height: 100%;
    `;

    handleClickBlur = () => {
        this.props.blur();
    }

    handlePoint = (point) => {
        this.props.selectPoint(point);
        this.props.blur();
    }

    componentDidMount(){
        setTimeout(() =>{
            this.setState({
                rendered: true
            })
        },0)
    }

    state = {
        rendered: false
    }

    render() {
        return (
                <this.Container style={this.state.rendered ? {transform: 'translate(0)'} : null}>
                    <this.Click onClick={() => this.handleClickBlur()}/>
                    <this.Scores>
                        <div>
                            <p>انتخاب امتیاز</p>
                            <i onClick={() => this.handleClickBlur()} className='fa fa-chevron-down'/>
                        </div>
                        <Link onClick={() => this.handlePoint('1')} to='/round'>
                            <span>1</span>
                            <i></i>
                        </Link>
                        <Link onClick={() => this.handlePoint('2')} to='/round'>
                            <span>2</span>
                            <i></i>
                        </Link>
                        <Link onClick={() => this.handlePoint('3')} to='/round'>
                            <span>3</span>
                            <i></i>
                        </Link>
                    </this.Scores>
                </this.Container>
        );
    }
}

export default connect(store => ({Setting: getSetting(store)}),{blur,selectPoint})(Popup);
