import React, { Component } from 'react';
import soundfile from '../assets/sounds/main.mp3';
import styled from 'styled-components'

class MainSound extends Component {

    state = {
        playing: false
    }

    handleSoundPlay = () => {
        this.setState(prev => {
            return {
                playing: !prev.playing
            }
        })
    }

    Button = styled.button`
        background-color: #fff;
        padding: .3rem .8rem;
        border-radius: 1.5rem;
        border: none;
        position: absolute;
        right: 7%;
        top: 4%;
        font-size: 2rem;
        color: #333;
        &:focus {
            outline: none;
        }
    `;

    componentDidMount(){
        // const audioCtx = new (window.AudioContext || window.webkitAudioContext)();
        // const source = audioCtx.createBufferSource();
        // const xhr = new XMLHttpRequest();
        // xhr.open('GET', soundfile);
        // xhr.responseType = 'arraybuffer';
        // xhr.addEventListener('load', function (r) {
        //     audioCtx.decodeAudioData(
        //             xhr.response, 
        //             function (buffer) {
        //                 source.buffer = buffer;
        //                 source.connect(audioCtx.destination);
        //                 source.loop = true;
        //             });
        //     source.start(0);
        // });
        // xhr.send();
    }

    render() {
        return (
            <>
                <this.Button onClick={() => this.handleSoundPlay()}>
                    {this.state.playing ? <i className="fas fa-volume-down"/> : <i className="fas fa-volume-mute"/>}
                </this.Button>
                <audio autoPlay loop={true} muted={this.state.playing ? false : true}>
                    <source src={soundfile} type="audio/mpeg"/>
                    Your browser does not support the audio element.
                </audio>
            </>
        );
    }
}

export default MainSound;
